import empty from "@/components/empty";
import { sendAjax } from '@/common/js/sendAjax.js';
import config from '@/apiConfig';
const { getFormId,getOrderList,updateOrderState} = config.api;
export default {
	components: {
		empty
	},
	data() {
		return {
			role:2, //模拟身份，1为用户，2为保洁员
			tabCurrentIndex: 0,
			navList: [{state: 0,text: '全部'},
				// {state: 1,text: '待预约'},
				{state: 1,text: '预约中'},
				{state:2,text: '已预约'},
				{state: 3,text: '已失败'},
				{state: 4,text: '已关闭'}
				// {state: 4,text: '服务中'},
				// {state: 5,text: '待评价'}
			],
			orderList:[],
			orderState:0,
			pageNum: 1,
			pageSize: 5
		};
	},
	
	onLoad(options){
		this.tabCurrentIndex = +options.state||0;
		this.getOrderList();
	},
	 
	methods: {
		//顶部tab点击
		tabClick(index){
			this.tabCurrentIndex = index;
			this.orderState = index;
			this.getOrderList();
		},
		cancelOrder(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			uni.showModal({
			    title: '提示',
			    content: '确认取消预约？',
			    success: function (res) {
			        if (res.confirm) {
			           var data = {
			           		 id:id,
			           		 orderState:3
			           	}
			           let infoOpt = {
			           	url: updateOrderState,
			           	type: 'PUT',
			           	data: JSON.stringify(data),
			           };
			           let infoCb = {};
			           infoCb.success = function(res) {
			           	console.log(res)
			           	if(res == null){
			           		uni.showToast({
			           			title:'取消成功'
			           		})
							that.getOrderList();
			           	}
			           	uni.hideLoading()
			           },
			           infoCb.beforeSend = () => {
			             uni.showLoading({
			             	title:'加载中',
							icon:'none'
			             })
			           };
			           sendAjax(infoOpt, infoCb,1);
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		confirmOrder(e){
			var that = this;
			var id = e.currentTarget.dataset.id;
			uni.showModal({
			    title: '提示',
			    content: '确认预约？',
			    success: function (res) {
			        if (res.confirm) {
			           var data = {
			           		 id:id,
			           		 orderState:2
			           	}
			           let infoOpt = {
			           	url: updateOrderState,
			           	type: 'PUT',
			           	data: JSON.stringify(data),
			           };
			           let infoCb = {};
			           infoCb.success = function(res) {
			           	console.log(res)
			           	if(res == null){
			           		uni.showToast({
			           			title:'确认成功'
			           		})
							that.getOrderList();
			           	}
			           	uni.hideLoading()
			           },
			           infoCb.beforeSend = () => {
			             uni.showLoading({
			             	title:'加载中',
							icon:'none'
			             })
			           };
			           sendAjax(infoOpt, infoCb,1);
			        } else if (res.cancel) {
			            
			        }
			    }
			});
		},
		getOrderList(){
			var pageNum = this.pageNum;
			var pageSize = this.pageSize;
			var employerId = uni.getStorageSync('userInfo').openId;
			var orderState = this.orderState;
			var that = this;
			if(this.orderState == 0){
				var data = {
					employerId:employerId,
					pageNum: pageNum,
					pageSize: pageSize
				}
			}else{
				var data = {
					employerId:employerId,
					orderState:orderState,
					pageNum: pageNum,
					pageSize: pageSize
				}
			}
			
			let infoOpt = {
				url: getOrderList,
				type: 'GET',
				data:data
			};
			let infoCb = {};
			infoCb.success = function(res) {
				that.orderList = res.list;
				uni.hideLoading()
			},
			infoCb.beforeSend = () => {
			  uni.showLoading({
			  	title:'加载中'
			  })
			};
			sendAjax(infoOpt, infoCb);
		},
		toDetail(e){
			var id = e.currentTarget.dataset.id;
			uni.navigateTo({
				url:'orderDetail/orderDetail?id='+id
			})
		},
		//收集formId
		  getFormId:function(e){
			  console.log(e)
		    var formId = e.detail.formId;
		    // var userId = wx.getStorageSync('userinfo').userId;
		    var openId = uni.getStorageSync('userinfo').openId;
		    console.log(formId)
		    // if (formId != 'the formId is a mock one'){
		    //   var that = this;
