		    //     url:getFormId,
		    //     type: 'POST',
		    //     data: {
		    //       // userId: userId,
		    //       openId: openId,
		    //       formId: formId
		    //     },
		    //     header: {
		    //       'content-type': 'application/json',
		    //     },
		    //   }
		    //   let infoCb = {}
		    //   infoCb.success = function (res) {
		    //   }
		    //   infoCb.beforeSend = () => { }
		    //   sendAjax(infoOpt, infoCb, () => { });
		    // }
		    
		  }
		
	},
}
